/* lato-100 - latin */
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 100;
  src: url('../Fonts/font/lato-v20-latin-100.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('../Fonts/font/lato-v20-latin-100.eot') format('embedded-opentype'), /* IE6-IE8 */
       url('../Fonts/font/lato-v20-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
       url('../Fonts/font/lato-v20-latin-100.woff') format('woff'), /* Modern Browsers */
       url('../Fonts/font/lato-v20-latin-100.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../Fonts/font/lato-v20-latin-100.svg#Lato') format('svg'); /* Legacy iOS */
}
